import React, { Component } from 'react'

import { connect } from 'react-redux';
import { toyService } from '../services/toyService';
import { removeToy } from '../store/actions/toyActions'

export class _ToyDetails extends Component {

    state = {
        toy: null
    }

    componentDidMount() {
        toyService.getById(this.props.match.params.id)
            .then(toy => {
                this.setState({ toy })
            })
    }
    onDeleteToy = () => {
        this.props.removeToy(this.state.toy)
        setTimeout(()=>this.props.history.push('/toy'),500)
        

    }

    render() {
        const { toy } = this.state
        if (!toy) return <span>Loading...</span>

        return (
            <div>
                <h3>{toy.name}</h3>
                <h4>{toy.price}$</h4>
                <h5>Category: {toy.type}</h5>
                {toy.inStock ? 'This toy is on stock!' : 'Out of stock'}
       <h5>Created At {toy.createdAt}</h5>
                <button onClick={this.onDeleteToy}>Delete Toy</button>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys
    }
}
const mapDispatchToProps = {
    removeToy
}


export const ToyDetails = connect(mapStateToProps, mapDispatchToProps)(_ToyDetails)
