import React from 'react'
import { NavLink } from 'react-router-dom'

export  function ToyPreview({toy}) {
    return (
        <div className="toy-preview">
            <h3>{toy.name}</h3>
            <h4>{toy.price}$</h4>
            <h5>Category: {toy.type}</h5>
            {toy.inStock&&'This toy is on stock!'}
            {!toy.inStock&&'Out of stock'}
            <div><NavLink to={`/toy/${toy._id}`}>Check It Out!</NavLink></div>
            <div><NavLink to={`/toy/edit/${toy._id}`}>Edit</NavLink></div>

        </div>
    )
}



